import React, { useEffect, useState } from "react";
import "./App.css";
import Navbar from "./components/navbar/navbar";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer } from "react-toastify";
import Home from "./components/main/home";
import Gallery from "./components/main/gallery";
import Footer from "./components/main/footer";
import About from "./components/main/about";
import Cart from "./components/cart/cart";
import Signup from "./components/user/signup";
import Signin from "./components/user/signin";
import Logout from "./components/user/logout";
import { Redirect, Route, Switch } from "react-router-dom";
import PageNotFound from "./components/main/pageNotFound";
import userService from "./services/userService";
import Signupbiz from "./components/user/signupBiz";
import CreateCard from "./components/card/createCard";
import CreateProduct from "./components/product/createProduct";
import ProtectedRoute from "./components/common/protectedRoute";
import MyCards from "./components/card/myCards";
import MyProducts from "./components/product/myProducts";
import AllProducts from "./components/product/allProducts";
import AllCards from "./components/card/allCards";
import EditMyCard from "./components/card/editMyCard";
import EditMyProduct from "./components/product/editMyProduct";
import Contact from "./components/main/contact";
import productDetails from "./components/product/productDetails";
import DisplayUserProducts from "./components/product/displayUserProducts";
import CheckoutForm from "./components/cart/checkout";

function App() {
  const [user, setUser] = useState(null);
  const [admin, setAdmin] = useState(null);

  useEffect(() => {
    setUser(userService.getCurrentUser())
    setAdmin(userService.adminEntery())
  }, [])

  console.log(user)
  console.log(admin)

  return (
    <div className="App d-flex flex-column min-vh-100">
      <ToastContainer />
      <header>
        <Navbar user={user} />
      </header>
      <main>
        <Switch>
          <ProtectedRoute
            path="/create-product"
            component={CreateProduct}
            biz={true}
            exact
          />
          <ProtectedRoute
            path="/create-card"
            component={CreateCard}
            biz={true}
            exact
          />
          <ProtectedRoute
            path="/display-my-cards/edit/:id"
            component={EditMyCard}
            biz={true}
            exact
          />
          <ProtectedRoute
            path="/display-my-cards"
            component={MyCards}
            biz={true}
            exact
          />
          <ProtectedRoute
            path="/display-my-products/edit/:id"
            component={EditMyProduct}
            biz={true}
            exact
          />
          <ProtectedRoute
            path="/display-my-products"
            component={MyProducts}
            biz={true}
            exact
          />
          <Route path="/" component={Home} exact />
          <Route path="/all-products" component={AllProducts} exact />
          <Route
            path="/product-details/:id"
            component={productDetails}
            exact
          />
          <Route
            path="/display-user-products/:id"
            component={DisplayUserProducts}
            exact
          />
          <Route path="/checkout-form" component={CheckoutForm} exact />
          <Route path="/contact" component={Contact} exact />
          <Route path="/cards" component={AllCards} exact />
          <Route path="/about" component={About} exact />
          <Route path="/signin" component={Signin} exact />
          <Route path="/signup" component={Signup} exact />
          <Route path="/biz" component={Signupbiz} exact />
          <Route path="/signout" component={Logout} exact />
          <Route path="/gallery" component={Gallery} exact />
          <Route path="/cart" component={Cart} exact />
          <Route path="/pageNotFound" component={PageNotFound} exact />
          <Redirect to="/pageNotFound" />
        </Switch>
      </main>
      <footer>
        <Footer />
      </footer>
    </div>
  );
}


export default App;
